// Import global vars
@import "master";
@import "fonts";

html,
body {
  height: 100%;
  padding: 0;
}
body {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  color: #000;
  background-color: #f7f7f7;
  // background-color: #fff;

  // &.shrinked {
  //   transform: scale(0.85);
  // }
}
body.lang-la {
  font-family: "Phetsarath OT", sans-serif;

  .main-header {
    font-family: "Phetsarath OT", sans-serif;
  }
}

// .main-header {
//   padding-left: 50px;
//   padding-right: 50px;
// }
.main-content {
  flex: 1 0 auto;
  padding-left: 50px;
  padding-right: 50px;
}
.main-footer {
  flex-shrink: 0;
}

img {
  max-width: 100%;
}

a {
  color: #da291c;
  transition: 200ms;
  &:hover {
    color: darken(#da291c, 5%);
  }
}

.body-freeze {
  border-right-width: 17px;
  border-right-style: solid;
  border-right-color: #fff;
}

.ty-button {
  border: none;
  outline: none;

  font-weight: bold;
  font-size: 26px;
  color: #fff;

  border-radius: 44px;
  background: #da291c;

  padding: 16px 34px;
  min-width: 220px;

  display: inline-block;
  text-align: center;
  text-decoration: none !important;
  color: #fff !important;

  transition: 200ms;
  &:hover {
    background: darken(#da291c, 5%);
  }

  &--grey {
    background: #d0d0d0;
    &:hover {
      background: darken(#d0d0d0, 5%);
    }
  }
  &--slim {
    font-size: 20px;
    padding: 10px 18px;
  }
}

.ty-custom-select-wrap {
  width: 280px;
  position: relative;
}
.ty-custom-select {
  padding: 22px 40px;
  height: auto;
  width: 100%;
  border-radius: 44px;
  background: #fff;
  font-weight: 500;
  font-size: 30px;
  color: #da291c;

  outline: none !important;
  box-shadow: none !important;
  border: none !important;
  background: #fafafa;

  padding-left: 84px;
  padding-right: 60px;

  &__icon-pre {
    position: absolute;
    top: 50%;
    left: 26px;
    transform: translateY(-50%);

    font-size: 40px;
    color: #da291c;
    pointer-events: none;
    user-select: none;
  }
  &__icon-after {
    position: absolute;
    top: 50%;
    right: 26px;
    transform: translateY(-50%);

    font-size: 13px;
    color: #a3a3a3;
    pointer-events: none;
    user-select: none;
  }
}

.main-header {
  position: relative;
  font-family: "Open Sans", sans-serif;

  &__logo {
    max-width: 174px;
  }
  &__content {
    padding-top: 26px;
    padding-bottom: 60px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    max-width: 1200px;
    margin: auto;
    padding-left: 17px;
    padding-right: 17px;
  }
  &__content-right {
    display: flex;
    margin-top: 8px;
    // align-items: center;
  }
  &__control-btn {
    margin-left: 20px;
    text-decoration: none !important;
    color: #1b365d;
    font-size: 15px;
    line-height: 20px;
    i {
      font-size: 20px;
    }
    &:hover {
      color: #1b365d;
    }

    &.active {
      color: #ed1b2e;
    }
  }

  &__languages-btn {
    margin-top: -1px;
    display: flex;
    align-items: center;
    font-style: italic;
    font-weight: 600;

    i {
      margin-right: 9px;
    }
  }

  &__top {
    position: relative;
    z-index: 900;
    // background-color: #f7f7f7;
    transition: 150ms linear;

    background: #fff;
    box-shadow: 0 0 8rem rgba(0, 0, 0, 0.08);
  }

  &__nav-wrap {
    position: absolute;
    z-index: 901;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 1180px;
    margin-top: 29px;
    // margin-right: auto;
    // margin-left: auto;
    // margin-bottom: -35px;
    padding-left: 7px;
    padding-right: 7px;

    transition: all 200ms linear, top 0ms linear;
  }
  &__nav {
    background-color: #ed1b2e;
    border-radius: 8px;
    z-index: 4;
    transition: background-color 0.2s linear, width 0.5s linear,
      border-radius 0.2s linear;

    &-menu {
      list-style: none;
      display: flex;
      padding: 0;
      margin: 0;
      justify-content: center;
      padding: 0 20px;

      &-item,
      &-link {
        color: #fff !important;
        text-decoration: none !important;
      }

      &-item {
        margin-left: 10px;
        margin-right: 10px;
        &:first-child {
          margin-left: 0;
        }
        &:last-child {
          margin-right: 0;
        }

        position: relative;
        &::after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          width: 0;
          height: 5px;
          background-color: #fff;
          border-radius: 2px 2px 0 0;
          transition: width 0.5s ease;
        }
        &.active {
          &::after {
            width: 100%;
          }
        }
      }
      &-link {
        font-size: 18px;
        line-height: 28px;
        font-weight: 600;
        display: inline-block;
        text-decoration: none;
        padding-bottom: 20px;
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 21px;
      }
    }
  }

  &__overlay-backdrop {
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    height: 100vh;
    background-color: rgba(#fff, 0.9);
    z-index: 890;

    &.active {
      display: block;
    }
  }
  &__overlay-panel {
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    // height: 100vh;
    // background-color: rgba(#fff, 0.9);
    z-index: 891;
    border-top: 1px solid #e5eaef;
    padding-left: 7px;
    padding-right: 7px;

    &-content {
      background: #fff;
      box-shadow: 0 0 16rem rgba(0, 0, 0, 0.1);
    }
    &-inner {
      max-width: 1170px;
      margin: auto;
      // padding-top: 76px;
      // padding-bottom: 60px;
      padding-left: 2px;
      padding-right: 2px;
    }

    &-link {
      position: relative;
      font-size: 22px;
      line-height: 32px;
      color: #000;
      text-decoration: none !important;
      &:hover {
        color: #000;
      }
      span {
        &:first-child {
          display: inline;
          padding-right: 6px;
        }
      }
      .i-icon {
        color: #ed1b2e;
        font-size: 13px;
      }
    }
    &-menu {
      list-style: none;
      padding: 0;
      margin: 0;

      margin-top: 12px;
      font-size: 20px;
      line-height: 28px;
      font-weight: 300;
      color: #000;
      text-decoration: none;

      li {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
      a {
        color: #000;
        text-decoration: none !important;
        &:hover {
          color: #000;
        }
      }
    }

    &.active {
      display: block;

      .main-header__overlay-content {
        transform: translateY(0);
      }
    }
  }

  &__mobile-burger {
    // position: absolute;
    // right: 0;
    position: relative;
    margin-right: -20px;
    background-color: #ed1b2e;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    font-size: 18px;
    color: #fff;
    margin-left: 24px;
    border: none;

    display: none;

    .i-icon-close {
      display: none;
    }
    &.active {
      .i-icon-burger {
        display: none;
      }
      .i-icon-close {
        display: block;
      }
    }
  }

  .panel-content {
    padding-top: 76px;
    padding-bottom: 60px;
    &--search {
      padding-top: 62px;
      padding-bottom: 30px;
    }
    &--language {
      padding-top: 73px;
      padding-bottom: 40px;
    }

    &__flex {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__title {
      white-space: nowrap;
      font-size: 20px;
      line-height: 28px;
      font-weight: 700;
      color: #000;
      // align-self: flex-start;
      margin-right: 60px;
      // margin-top: 10px;
    }
    &__content {
      flex-grow: 1;
      // overflow-y: auto;

      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
    &__btn-close {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      background-color: #f7f7f7;
      border-radius: 50%;
      text-decoration: none;
      color: #000;
      margin-left: 60px;
      // margin-top: 4px;

      i {
        font-size: 14px;
      }
      span {
        display: none;
      }
    }

    .language {
      &__item-btn {
        align-items: center;
        font-size: 16px;
        line-height: 24px;
        font-weight: 600;
        color: #000;
        border: 2px solid #e5eaef;
        padding: 10px 22px 8px 22px;
        border-radius: 50px;
        text-decoration: none;
        margin-top: -1px;

        display: block;
        &:not(:last-child) {
          margin-right: 32px;
        }

        &.active {
          border-color: #ed1b2e;
        }
      }
    }
  }

  .panel-search-form {
    width: 100%;
    position: relative;
    top: 0;
    left: 0;

    &__content {
      background-color: #fff;
      // box-shadow: 0 26px 30px rgba(0, 0, 0, 0.05);
      border-radius: 8px;
      width: 100%;
      display: flex;
      align-items: center;

      padding: 17px 20px;
      border: 2px solid #e5eaef;
      box-shadow: none;
    }

    &__input {
      width: calc(100% - 30px);
      font-size: 18px;
      line-height: 28px;
      padding-left: 13px;
      border: none;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      text-decoration: none;

      padding-left: 20px;

      color: #000;
      // font-weight: 600;
      outline: 0;
    }

    &__submit-btn {
      width: 22px;
      height: 24px;

      background-color: transparent;
      border: none;
      padding: 0;
      outline: 0;
      float: left;

      i {
        color: #ed1b2e;
        font-size: 22px;
        line-height: 23px;
        width: 23px;
        height: 23px;
        display: inline-block;
        margin-top: 1px;
      }
    }
    &__clear-btn {
      display: none;
      width: 22px;
      height: 24px;

      background-color: transparent;
      border: none;
      padding: 0;
      outline: 0;
      float: left;

      i {
        color: #ed1b2e;
        font-size: 22px;
        line-height: 23px;
        width: 23px;
        height: 23px;
        display: inline-block;
        margin-top: 1px;
      }
    }

    &.active {
      .panel-search-form__content {
        border-color: transparent;
        box-shadow: 0 26px 30px rgba(0, 0, 0, 0.05);
      }
      .panel-search-form__submit-btn {
        display: none;
      }
      .panel-search-form__clear-btn {
        display: block;
      }
    }
  }

  &.sticky {
    .main-header__top {
      transform: translateY(-100%);
    }
    .main-header__nav-wrap {
      position: fixed;
      z-index: 1000;
      max-width: 100%;
      top: 0;
      margin-top: 0;
      padding-left: 0;
      padding-right: 0;
    }
    .main-header__nav {
      // max-width: none;
      // left: 0;
      // transform: none;
      border-radius: 0;
      background-color: #fff;
      box-shadow: 0 0 8rem rgba(0, 0, 0, 0.08);
    }
    .main-header__nav-menu {
      &-item,
      &-link {
        color: #000 !important;
      }
    }

    .main-header__nav-menu-item {
      &::after {
        background-color: #ed1b2e;
      }
    }
  }
}
.mobile-menu-overlay {
  display: none;

  position: fixed;
  top: 60px;
  background: #fff;
  width: 100%;

  border-top: 1px solid #eee;
  overflow-y: auto;
  z-index: 888;
  padding-bottom: 76px;
  will-change: auto;

  // height: 100%;
  max-height: calc(100vh - 4rem);

  /* width */
  &::-webkit-scrollbar {
    width: 4px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: #888;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  &__menu {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
    padding-left: 26px;
    padding-right: 26px;

    & > li {
      & > a {
        position: relative;
        display: block;
        font-size: 20px;
        line-height: 27px;
        padding: 16px 0;
        color: #000;
        text-decoration: none;

        .i-icon {
          position: absolute;
          top: 50%;
          -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
          right: 0;
          color: #ed1b2e;
          font-size: 13px;
          -webkit-transition: all 0.2s linear;
          transition: all 0.2s linear;
          -webkit-transform-origin: 50% 25%;
          transform-origin: 50% 25%;
        }

        &.open {
          .i-icon {
            transform: rotate(180deg);
          }
        }
      }
    }
    & > li:not(:last-child) {
      border-bottom: 1px solid #e5eaef;
    }
  }
  &__menu-panel {
    display: none;

    & > ul {
      list-style: none;
      margin-bottom: 0;
      padding-left: 0;

      & > li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
        &:last-child {
          padding-bottom: 8px;
        }

        & > a {
          width: 100%;
          margin-bottom: 0;

          display: block;
          font-size: 18px;
          line-height: 28px;
          font-weight: 300;
          color: #000;
          text-decoration: none;
        }

        .right-icon {
          padding-left: 20px;
          padding-right: 6px;
          display: flex;
          align-items: center;
          pointer-events: none;
          position: absolute;
          right: 25px;

          i {
            color: #ed1b2e;
            transition: all 0.2s linear;
          }
        }
      }

      .mobile-menu-overlay__inner-menu-wrap {
        display: block;
        justify-content: unset;
        align-items: unset;
      }
      .mobile-menu-overlay__inner-menu {
        position: fixed;
        top: 60px;
        left: -100%;
        bottom: 0;
        right: 0;
        overflow: hidden;
        width: 100%;
        background: #fff;
        min-height: 100vh;
        z-index: 1100;
        padding: 0 26px;
        padding-top: 16px;
        transition: left 0.3s ease;

        &.active {
          left: 0%;
        }

        &-content {
          margin-left: 10px;
        }
        &-btn-wrap {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        &-btn {
          display: block;
          font-size: 18px;
          line-height: 28px;
          font-weight: 300;
          color: #000;
          text-decoration: none;
          width: 100%;
        }
        &-list {
          list-style: none;
          padding-left: 0;
          margin-left: 0;
          margin-top: 12px;
          padding-left: 16px;
          li {
            padding: 10px 0;
            display: flex;
            a {
              font-size: 18px;
              line-height: 28px;
              font-weight: 300;
              color: #000;
              text-decoration: none;
              width: 100%;
            }
          }
        }
        .back-icon {
          margin-left: -20px;
          a {
            cursor: pointer;
            position: relative;
            font-size: 20px;
            line-height: 32px;
            color: #000;
            text-decoration: none;

            i {
              color: #ed1b2e;
              transition: all 0.2s linear;
              font-size: 14px;
              margin-right: 10px;
            }
          }
        }
      }
    }
  }
  &__language {
    margin-top: 44px;
    margin-bottom: 16px;

    padding-left: 26px;
    padding-right: 26px;

    &-select-wrap {
      position: relative;
      margin-bottom: 10px;
    }
    &-select {
      position: absolute;
      top: 0;
      left: 0;
      right: 5px;
      width: 100%;
      height: 50px;
      z-index: 1;
      opacity: 0;

      &-btn {
        position: relative;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        border: 2px solid #e5eaef;
        border-radius: 8px;
        font-size: 14px;
        line-height: 16px;
        font-weight: 700;
        font-style: italic;
        width: 100%;
        height: 50px;
        padding: 0 5px;
        text-decoration: none;
        background-color: #fff;
        color: #1b365d;
        z-index: 4;

        pointer-events: none;

        i {
          font-size: 20px;
          margin-right: 8px;
        }
      }
    }
  }
}
