.stepper {
  &__header {
    text-align: center;
    margin-bottom: 20px;
  }
  &__title {
    font-weight: bold;
    font-size: 30px;
    // color: #da291c;
    color: #000;
  }

  &__bullets {
    user-select: none;
    pointer-events: none;

    margin-top: auto;
    display: flex;
    justify-content: space-between;

    width: 500px;
    margin: auto;

    margin-bottom: 20px;

    &-item {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 1;

      &::before {
        position: absolute;
        content: "";
        border-bottom: 10px solid #d0d0d0;
        width: 100%;
        top: 20px;
        left: -50%;
        z-index: 2;
      }
      &::after {
        position: absolute;
        content: "";
        border-bottom: 10px solid #d0d0d0;
        width: 100%;
        top: 20px;
        left: 50%;
        z-index: 2;
      }

      &-counter {
        position: relative;
        z-index: 5;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background: #d0d0d0;
        margin-bottom: 6px;

        color: #fff;
        width: 50px;
        height: 50px;
        font-size: 34px;
        font-weight: bold;
        border: 2px solid #fff;
      }

      &.completed {
        .stepper__bullets-item-counter {
          background-color: #da291c;
        }
        &::after {
          border-color: #da291c;
          z-index: 3;
        }
      }
      &.active {
        &::before {
          z-index: 3;
        }
      }

      &:first-child::before {
        content: none;
      }
      &:last-child::after {
        content: none;
      }
    }
  }

  &__content {
    margin-bottom: 30px;
  }
  &__footer {
    display: flex;
    justify-content: space-between;

    & > * {
      &:not(:last-child) {
        margin-right: 15px;
      }
    }
  }
}

.field-wrapper {
  display: flex;
  align-items: center;

  margin-bottom: 15px;

  &--vertical {
    flex-direction: column;
    align-items: flex-start;

    .field-label {
      width: auto;
    }
  }
}
.field-label-wrap {
  width: 300px;
  position: relative;
  min-height: 45px;

  display: flex;
  // flex-direction: column;
  align-items: center;
  flex: 0 0 auto;
  margin-right: 20px;
}
.field-label {
  display: inline-flex;
  align-items: center;
  flex: 0 0 auto;

  font-weight: normal;
  font-size: 18px;
  color: #505050;

  margin-bottom: 0;

  &__icon {
    font-size: 30px;
    color: #da291c;
    margin-right: 20px;
  }

  &--column {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

